<template>
  <div>
    <nav class="navbar navbar-dark bg-light fixed-top shadow">
      <a class="navbar-brand"></a>
      <form class="form-inline">
        <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#adminselfupdate">{{ username }}</button>
        <button type="button" class="btn btn-primary" >{{ institute }}</button>
        <button type="button" class="btn btn-primary" >{{ membertype }}</button>
        <button class="btn btn-outline-success my-2 my-sm-0" type="submit"
          @click="logout"
        ></button>
      </form>
    </nav>
    <div class="modal fade bd-example-modal-lg" tabindex="-1" role="dialog" aria-labelledby="myLargeModalLabel" aria-hidden="true" id="adminselfupdate">
      <div class="modal-dialog modal-lg">
        <div class="modal-content">
          <div class="container">
            <form>
              <div class="form-group">
                <label for="username"></label>
                <input type="text" class="form-control" id="username" disabled 
                  :placeholder="username" 
                >
              </div>
              <div class="form-group">
                <label for="realname"></label>
                <input type="text" class="form-control" id="realname" placeholder=""
                  v-model="realname"
                >
              </div>
              <div class="form-group">
                <label for="bankcard"></label>
                <input type="text" class="form-control" id="bankcard" placeholder=""
                  v-model="bankcard"
                >
              </div>
              <div class="form-group">
                <label for="bankinfo"></label>
                <input type="text" class="form-control" id="bankinfo" placeholder=""
                  v-model="bankinfo"
                >
              </div>
              <div class="form-group">
                <label for="email">Email</label>
                <input type="text" class="form-control" id="email" placeholder="Email"
                  v-model="email"
                >
              </div>
              <div class="form-group">
                <label for="institute"></label>
                <select class="custom-select d-block w-100" id="country" required
                  v-model="institute"
                >
                  <option disabled value=""></option>
                  <option></option>
                  <option></option>
                  <option></option>
                </select>
              </div>
              <div class="form-group">
                <label for="passwordorg"></label>
                <input type="password" class="form-control" id="passwordorg" placeholder=""
                  v-model="passwordorg"
                >
              </div>
              <div class="form-group">
                <label for="passwordnew"></label>
                <input type="password" class="form-control" id="passwordnew" placeholder=""
                  v-model="passwordnew"
                >
              </div>
              <button type="submit" class="btn btn-primary"
                @click="update"
              ></button>
              <p>{{ updatemsg }}</p>
            </form>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="row">
        <nav class="col-md-2 d-none d-md-block bg-light sidebar">
          <div class="list-group " id="list-tab" role="tablist">
            <a class="list-group-item list-group-item-action " id="list-home-list" data-toggle="list" href="#list-home" role="tab" aria-controls="home"
              v-for="tab in tabs"
              :key="tab"
              :class="{active : currentTab === tab}"
              @click="currentTab = tab"
            >{{tab}}</a>
          </div>
        </nav>
        <div class="col-md-10">
          <component
            :is="currentTabComponent"
          ></component>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import ProjManage from '@/components/AdminPage_sub/ProjManage.vue'
import UserManage from '@/components/AdminPage_sub/UserManage.vue'
import CourseManage from '@/components/AdminPage_sub/CourseManage.vue'
import ShareholdManage from '@/components/AdminPage_sub/ShareholdManage.vue'

export default {
  name: 'AdminPage',
  props: {
    username  : '',
    realname  : '',
    bankinfo  : '',
    bankcard  : '',
    institute : '',
    membertype: '',
    email     : '',
  },
  components : {
    ProjManage,
    UserManage,
    CourseManage,
    ShareholdManage
  },
  data  : function(){
    return  {
      passwordorg: '',
      passwordnew: '',
      updatemsg : '',
      currentTab: '',
      tabs  : ['','','','']
    }
  },
  computed  : {
    currentTabComponent:  function(){
      if(this.currentTab == ''){
        return 'ProjManage';
      }
      else if(this.currentTab == ''){
        return 'UserManage';
      }
      else if(this.currentTab ==  ''){
        return 'CourseManage'
      }
      else if(this.currentTab == ''){
        return 'ShareholdManage'
      }
    }
  },
  methods : {
    logout  : function(){
      this.$router.replace('/');
    },
    update  : function(){
      if(this.passwordorg == ''){
        this.updatemsg  = '';
      }
      else {
        this.$http.post('/users/update',{
          kind      : 'selfupdate',
          username  : this.username,
          realname  : this.realname,
          bankinfo  : this.bankinfo,
          bankcard  : this.bankcard,
          institute : this.institute,
          email     : this.email,
          passwordorg : this.passwordorg,
          passwordnew : this.passwordnew
        }).then(
          function(response){
            if(response.body.ok ==  'ok'){
              this.updatemsg  = '';
            }
            else if(response.body.ok  ==  'notok'){
              this.updatemsg  = '';
            }
          },
          function(){
          }
        );
      }
    }
  },
}
</script>

