<template>
  <div class="container">
    <router-link to="/login"></router-link> |
    <router-link to="/create"></router-link>
    <form>
      <div class="form-group">
        <label for="username"></label>
        <input type="text" class="form-control" id="username" placeholder=""
          v-model="username"
        >
      </div>
      <div class="form-group">
        <label for="realname"></label>
        <input type="text" class="form-control" id="realname" placeholder=""
          v-model="realname"
        >
      </div>
      <div class="form-group">
        <label for="bankcard"></label>
        <input type="text" class="form-control" id="bankcard" placeholder=""
          v-model="bankcard"
        >
      </div>
      <div class="form-group">
        <label for="bankinfo"></label>
        <input type="text" class="form-control" id="bankinfo" placeholder=""
          v-model="bankinfo"
        >
      </div>
      <div class="form-group">
        <label for="email">Email</label>
        <input type="text" class="form-control" id="email" placeholder="Email"
          v-model="email"
        >
      </div>
      <div class="form-group">
        <label for="institute"></label>
        <select class="custom-select d-block w-100" id="country" required
          v-model="institute"
        >
          <option disabled value=""></option>
          <option></option>
          <option></option>
          <option></option>
        </select>
      </div>
      <div class="form-group">
        <label for="password"></label>
        <input type="password" class="form-control" id="password" placeholder=""
          v-model="password"
        >
      </div>
      <div class="form-group">
        <label for="passwordag"></label>
        <input type="password" class="form-control" id="passwordag" placeholder=""
          v-model="passwordag"
        >
      </div>
      <button type="submit" class="btn btn-primary"
        @click="create"
      ></button>
      <p>{{ msg }}</p>
    </form>
  </div>
</template>

<script>
export default {
  name: 'CreatePage',
  props: {
  },
  data: function(){
    return {
      username  : '',
      realname  : '',
      bankcard  : '',
      bankinfo  : '',
      email     : '',
      institute : '',
      password  : '',
      passwordag: '',
      msg       : ''
    }
  },
  methods : {
    create  : function(){
      if(this.password  != this.passwordag){
        this.msg  = ''
      }
      else if(this.username == ''){
        this.msg  = ''
      }
      else  {
        this.$http.post('/users/create',{
          username  : this.username ,
          realname  : this.realname ,
          bankcard  : this.bankcard ,
          bankinfo  : this.bankinfo ,
          email     : this.email    ,
          institute : this.institute,
          password  : this.password ,
        }).then(
        function(response){
          if(response.body.ok == 'ok'){
            this.msg  = response.body.msg;
            this.username   = '';
            this.realname   = '';
            this.bankcard   = '';
            this.bankinfo   = '';
            this.email      = '';
            this.institute  = '';
            this.password   = '';
            this.passwordag = ''; 
          }
          else if(response.body.ok == 'notok'){
            this.msg  = response.body.msg;
          }
        },
        function(response){
          this.msg  = response.body.msg
        });
      }
    }
  }
}
</script>

