                label=""
              >
              </el-table-column>
              <el-table-column
                fixed="right"
                label=""
              >
                <template slot-scope="scope">
                  <el-button
                    @click.native.prevent="deleteRow(scope.$index, tableData4)"
                    type="text"
                    size="small">
                    
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
            <button type="button" class="btn btn-primary"></button>
          </div>
        </div>
      </div>
      <div class="modal fade bd-example-modal-lg" tabindex="-1" role="dialog" aria-labelledby="myLargeModalLabel" aria-hidden="true"
        :id="'projectedit'+oneproject.projectname"
      >
        <div class="modal-dialog modal-lg">
          <div class="modal-content">
            <table class="table table-bordered">
              <tbody>
                <tr class="table-info">
                  <th scope="row"></th>
                  <td colspan="2">
                    <input 
                      v-model="oneproject.projectname" disabled
                    >
                  </td>
                </tr>
                <tr>
                  <th scope="row"></th>
                  <td colspan="2">
                    <input 
                      v-model="oneproject.targetmoney"
                    >
                  </td>
                </tr>
                <tr>
                  <th scope="row"></th>
                  <td colspan="2">
                    <input 
                      v-model="oneproject.actualmoney" disabled
                    >
                  </td>
                </tr>
                <tr>
                  <th scope="row"></th>
                  <td colspan="2">
                    <select class="custom-select d-block w-100" id="country" required
                      v-model="oneproject.isvalid"
                    >
                      <option disabled value=""></option>
                      <option></option>
                      <option></option>
                    </select>
                  </td>
                </tr>
                <tr>
                  <th scope="row"></th>
                  <td>
                    <input 
                      v-model="oneproject.roadshowstart"
                    >
                  </td>
                  <td>
                    <input 
                      v-model="oneproject.roadshowend"
                    >
                  </td>
                </tr>
                <tr>
                  <th scope="row"></th>
                  <td>
                    <input 
                      v-model="oneproject.subscribestart"
                    >
                  </td>
                  <td>
                    <input 
                      v-model="oneproject.subscribeend"
                    >
                  </td>
                </tr>
                <tr>
                  <th scope="row"></th>
                  <td>
                    <input 
                      v-model="oneproject.confirmstart"
                    >
                  </td>
                  <td>
                    <input 
                      v-model="oneproject.confirmend"
                    >
                  </td>
                </tr>
              </tbody>
            </table>
            <button type="submit" class="btn btn-primary"
              @click="updateoneproject(oneproject.projectname)"
            ></button>
            <p>{{ updatemsg }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProjManage',
  props: {
  },
  data  : function(){
    return  {
      createmsg     : '',
      updatemsg     : '',
      projectname   : '',
      targetmoney   : '',
      roadshowstart : '',
      roadshowend   : '',
      subscribestart: '',
      subscribeend  : '',
      confirmstart  : '',
      confirmend    : '',
      projectlist   : [],
      tableData4    : [
        {
          slvusername : 'pengxu',
          institute   : ''
        },
      ],
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      value: ''
    }
  },
  methods : {
    create  : function(){
      this.createmsg  = '';
      if(this.projectname ==  ''){
        this.createmsg  = '';
      }
      else  {
        this.$http.post('/projects/create',{
          kind          : 'create',
          projectname   : this.projectname    ,
          targetmoney   : this.targetmoney    ,
          roadshowstart : this.roadshowstart  ,
          roadshowend   : this.roadshowend    ,
          subscribestart: this.subscribestart ,
          subscribeend  : this.subscribeend   ,
          confirmstart  : this.confirmstart   ,
          confirmend    : this.confirmend     
        }).then(
          function(response){
            if(response.body.ok ==  'ok'){
              this.createmsg  = response.body.msg;
              this.projectname     = '';
              this.targetmoney     = '';
              this.roadshowstart   = '';
              this.roadshowend     = '';
              this.subscribestart  = '';
              this.subscribeend    = '';
              this.confirmstart    = '';
              this.confirmend      = '';
              this.findallprojects();
            }
            else if(response.body.ok ==  'notok'){
              this.createmsg  = response.body.msg;
              this.projectname     = '';
              this.targetmoney     = '';
              this.roadshowstart   = '';
              this.roadshowend     = '';
              this.subscribestart  = '';
              this.subscribeend    = '';
              this.confirmstart    = '';
              this.confirmend      = '';
            }
          },
          function(){}
        );
      }
    },
    updateoneproject : function(projname){
      this.updatemsg  = '';
      for(var i=0;i<this.projectlist.length;i++){
        if(this.projectlist[i].projectname == projname){
          this.$http.post('/projects/update',{
            kind            : 'oneprojectupdate',
            projectname     : this.projectlist[i].projectname    ,
            targetmoney     : this.projectlist[i].targetmoney    ,
            roadshowstart   : this.projectlist[i].roadshowstart  ,
            roadshowend     : this.projectlist[i].roadshowend    ,
            subscribestart  : this.projectlist[i].subscribestart ,
            subscribeend    : this.projectlist[i].subscribeend   ,
            confirmstart    : this.projectlist[i].confirmstart   ,
            confirmend      : this.projectlist[i].confirmend     ,
            isvalid         : this.projectlist[i].isvalid        ,
            subscribesum    : this.projectlist[i].subscribesum   ,
            confirmsum      : this.projectlist[i].confirmsum     ,
          }).then(
            function(response){
              if(response.body.ok ==  'ok'){
                this.updatemsg  = '';
              }
              else if(response.body.ok ==  'notok'){
                this.updatemsg  = '';
              }
            },
            function(){
              this.updatemsg  = '';
            }
          );
        }
      }

    },
    findallprojects : function(){
      this.$http.post('/projects/find',{
        kind    :     'findall'
      }).then(
        function(response){
          if(response.body.ok ==  'ok'){
            this.projectlist  = response.body.projectlist;
          }
          else if(response.body.ok  ==  'notok'){
            this.projectlist  = [];
          }
        },
        function(){
          this.projectlist  = [];
        }
      );
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    }
  },
  mounted : function () {
    this.$http.post('/projects/find',{
      kind    :     'findall'
    }).then(
      function(response){
        if(response.body.ok ==  'ok'){
          this.projectlist  = response.body.projectlist;
        }
        else if(response.body.ok  ==  'notok'){
          this.projectlist  = [];
        }
      },
      function(){}
    );
  },
}
</script>

